"use strict";
exports.__esModule = true;
var hljs = require("highlight.js");
/**
 * Add navbar class on scroll down
 */
exports.addNavbarClassOnScroll = function () { return window
    .addEventListener('scroll', function () {
    var navbar = document.querySelector('#primaryNavbar');
    if (window.pageYOffset > 10) {
        navbar.classList.add('c-navbar--scroll');
    }
    else {
        navbar.classList.remove('c-navbar--scroll');
    }
}); };
/**
 * Navbar toggle
 */
exports.burgerAction = function () { return document
    .querySelector('#burgerSidebarMenu')
    .addEventListener('click', function () {
    var menu = document.querySelector('#headerNavbar');
    var main = document.querySelector('main');
    if (menu.className.indexOf('d-block') === -1) {
        menu.classList.add('d-block');
        menu.classList.remove('d-none');
        main.classList.add('navbar-is-open');
    }
    else {
        menu.classList.add('d-none');
        menu.classList.remove('d-block');
        main.classList.remove('navbar-is-open');
    }
}); };
/**
 * Highlight DOM code
 */
exports.codeHighlights = function () {
    hljs.configure({ useBR: true });
    var code = document.querySelectorAll('code');
    for (var i = 0; i < code.length; i++) {
        hljs.highlightBlock(code[i]);
    }
};
